
@font-face {
  font-family: "GOT";
  font-style: normal;
  font-weight: 400;
  src: local("GOT"), local("GOT"), url("./fonts/got.ttf") format("truetype");
}

body,
html {
  margin: 0;
  padding: 0;
  font-family: "Lato", sans-serif;
}

body{
  min-height: 100vh;
}

.App {
  margin: auto;
  background: white;
  min-height: 100vh;
}

header {
  font-family: GOT;
  padding: 20px 5px;
  font-weight: bold;
  text-align: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  margin-bottom: 30px;
  background: white;
}

button:focus{
  border: none;
  outline: none;
}

.app-tasks{
    /* background: red; */
    width:80%;
    display: flex;
    margin: auto;
}


.all-tasks{
  position: relative;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);
  background-color: rgb(255, 255, 255);
  border-top: 1px solid rgb(229, 227, 221);
  border-bottom: 1px solid rgb(229, 227, 221);
  border-left: 1px solid rgb(229, 227, 221);
  border-right: 1px solid rgb(229, 227, 221);
  border-radius: 10px;
   height:auto;
   width:70%;
   margin: auto;
   padding-top: 10%;
   padding-bottom: 10%;
}

.all-tasks{
  padding:2%
}
.all-tasks a{
  cursor: pointer;
}
.all-tasks-img{
  width: 20%;
  margin-bottom: 30px;
}
.all-tasks-img img{
  width:30px
}

.all-tasks-title {
  float: right;
  /* background: red; */
  width: 80%;
  padding-top: 0px;
   text-align: left !important;
}
.all-tasks-title p {
  position: relative;
  right:11%;
  bottom: 25px;
}
.all-tasks-title h3 {
  position: relative;
  right:11%;
   bottom: 14px;
}
.delete-and-edit{
  display: inline;
  margin-top: 10%;
}
.button {
  background-color: #4CAF50; /* Green */
  border: none;
  color: white;
  padding: 10px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
}
.all-tasks-body{
  position: relative;
  bottom: 30px;
  left: 10px;
}
.profile-loader {
  background: rgba(32, 32, 32, 0.1);
  z-index: 2000;
  position: fixed;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
}
.profile-modal-body{
  width: 100%;
  background: #fff;
  box-shadow: none;
  padding: 5%;
  background-color: rgb(255, 255, 255);
  border-top: 1px solid rgb(229, 227, 221);
  border-bottom: 1px solid rgb(229, 227, 221);
  border-left: 1px solid rgb(229, 227, 221);
  border-right: 1px solid rgb(229, 227, 221);
  border-radius: 4px;
  padding-top: 10%;
  overflow: hidden;
  padding-bottom: 10%;
}
.profile-modal-close{
  position: relative;
  float:right;
  top:40px;
  right:4%
}
.profile-pre-loader{
    background: #fff;
    width: 80%;
    height: 340px;
    margin: auto;
    margin-top: 100px;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);
    border-radius: 20px;
   padding:20px
}
.App-modal{
  
}
.App-modal-title{
  border-bottom: solid blue 2px;

}
.App-modal-title p{
 font-size: 180%;
}
.App-modal-title small{
  font-size: 120%;
  float: right;
 }
.App-modal-full{
  padding-top: 3%;
  padding-bottom: 2%;
}

input[type=text], select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.button4 {border-radius: 12px;}

.newTake-take{
  background: red;
  margin-bottom: 7%;
}
.app-new-form{
  width:50%;
  margin: auto;
}
.newTake-take a{
  float: right;
  position: relative;
  right: 5%;
}